import React, {Component} from 'react';
import styled from 'styled-components';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {loadAllClaims, sendPartnerData} from 'store';

import {ScanButton, TextInput} from 'ui';

const Form = styled.form`
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin-top: 20px;
`;

const SubmitButton = ScanButton.extend`
  margin-top: 15px;
`;

class PartnerRequests extends Component {
  componentDidMount() {
    this.props.loadAllClaims();
  }

  getCurrentClaims = () => {
    return this.props.claims.filter(item => this.props.partner.claims.indexOf(item.id) !== -1)
  };

  onSend = async (event) => {
    event.preventDefault();

    const id = {
      phone: '0b84dd79-22bc-4833-af53-a787019bfcb8',
      email: '21cd1a1e-1877-477a-b1eb-b7911e505bc6'
    };

    const claims = this.getCurrentClaims().reduce((acc, item) => {
      return [...acc, {
        claim_id: id[item.id],
        type: item.id,
        value: event.target[item.id].value
      }]
    }, []);

    await this.props.sendPartnerData(this.props.sessionId, {claims})
  };

  render() {
    const {partner} = this.props;

    return (
     <div>
       {partner ? (
        <RequiredFields partner={partner} claims={this.getCurrentClaims()} onSubmit={this.onSend}/>
       ) : (
        <PartnerAbsent/>
       )}
     </div>
    );
  }
}

const RequiredFields = ({partner, claims, onSubmit}) => (
 <div>
   <div>{partner.description}:</div>
   <Form onSubmit={onSubmit}>
     {
       claims
        .map(item => (
         <TextInput key={item.id} name={item.id} label={item.description}/>
        ))
     }
     <SubmitButton type='submit'>Send</SubmitButton>
   </Form>
 </div>
);

const PartnerAbsent = () => (
 <div>
   <div>You need scan QR-code</div>
   <ScanButton margin='20px 0 0'><Link to='/scan'>Scan QR-code</Link></ScanButton>
 </div>
);

const mapStateToProps = (state, ownProps) => ({
  partner: state.partner.data,
  claims: state.claims.data,
  sessionId: state.partner.sessionId
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  loadAllClaims: () => dispatch(loadAllClaims()),
  sendPartnerData: (sessionId, data) => (dispatch(sendPartnerData(sessionId, data)))
});

const Connected = connect(mapStateToProps, mapDispatchToProps)(PartnerRequests);

export {Connected as PartnerRequests};


