import React, {Component} from 'react';
import styled from 'styled-components';
import {connect} from 'react-redux';
import {loadPartner, LOADING, partnerActions} from 'store';

import {TextInput, ScanButton} from 'ui';

const Wrapper = styled.div`
  width: 100%;
`;

class ScanQR extends Component {
  onChange = ({target}) => {
    this.props.setSessionId(target.value);
  };

  scanQR = async () => {
    await this.props.loadPartner(this.props.sessionId);

    if (this.props.loading === LOADING.ready) {
      this.props.history.push('/partner');
    }
  };

  render() {
    return (
     <Wrapper>
       <TextInput name='qr-input' label='Enter QR-code' onChange={this.onChange}
                  value={this.props.sessionId}/>
       <ScanButton onClick={this.scanQR} fluid margin='10px 0 0'>Scan</ScanButton>
     </Wrapper>
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  loading: state.partner.status,
  sessionId: state.partner.sessionId
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  loadPartner: (sessionId) => dispatch(loadPartner(sessionId)),
  setSessionId: (sessionId) => dispatch(partnerActions.setSessionId(sessionId))
});

const ConnectedScanQR = connect(mapStateToProps, mapDispatchToProps)(ScanQR);

export {ConnectedScanQR as ScanQR};



